import React from "react";
import "./Cards.css";

/**
 * react single-card component
 */
function SingleCard() {
    return (
        <div className="column is-4 card">
            <center><h1 className="card-heading">WHY CHOOSE US ?</h1></center>
            <center><img src="/JEEC-Version-2/imgs/mentorship.jpg" alt="why-choose-us" className="card-image"/></center>
            <center>
            <h6 className="card-text">
                We will connect to you an IITian who will be
                your friend throughout your jee journey. You
                can connect him 24X7 on-call or WhatsApp for... 
            </h6>
            </center>
        </div>
    );
}

/**
 * react cards component
 */
function Cards() {
    return (
        <div id="cards" className="cards">
            <div className="columns">


                <SingleCard />
                <SingleCard />
                <SingleCard />


            </div>
        </div>
    );
}

export default Cards;